// Import fonts.
@font-face {
    font-family: 'seothemes-icons';
    src: url('assets/fonts/seothemes-icons.eot');
    src: url('assets/fonts/seothemes-icons.eot?#iefix') format('embedded-opentype'), url('assets/fonts/seothemes-icons.woff') format('woff'), url('assets/fonts/seothemes-icons.ttf') format('truetype'), url('assets/fonts/seothemes-icons.svg#seothemes-icons') format('svg');
    font-weight: normal;
    font-style: normal;
}


/* Use the following CSS code if you want to use data attributes for inserting your icons */

[data-icon]:before {
    font-family: 'seothemes-icons';
    content: attr(data-icon);
    speak: none;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: inline-block;
}

.icon-styles-page {
    .entry-content {
        p {
            display: block;
            clear: both;
            text-align: left;
        }
        i {
            margin: .382em;
        }
    }
}

[class*="icon-"] {
    &.gradient {
        width: 1.5em;
        @include gradient-text;
    }
    &.large {
        font-size: $font-size-heading * 2.5;
    }
    &.circle {
        background-color: $color-text;
        color: $color-white;
        height: 2.5em;
        width: 2.5em;
        line-height: 2.5em;
        border-radius: 999px;
        padding: 0;
        @include transition;
        &:hover {
            box-shadow: inset 0 0 0 999px rgba(255, 255, 255, .1618);
        }
        &.large {
            font-size: $font-size-heading * 1.5;
        }
        &.gradient {
            @include gradient;
            -webkit-background-clip: none;
            background-clip: none;
            -webkit-text-fill-color: $color-white;
            text-fill-color: $color-white;
        }
    }
}

