/* ## Search Form
--------------------------------------------- */
.search-form {
  display: block;
  width: 100%;
  clear: both;
}

.search-form:before, .search-form:after {
  display: block;
  content: '';
}

.search-form:after {
  clear: both;
}

.search-form input[type="search"] {
  float: left;
