  float: left;
  width: 100%;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

@media (min-width: 600px) {
  .home .front-page-3 .listing-item {
    width: 50%;
  }
}

@media (min-width: 860px) {
  .home .front-page-3 .listing-item {
    width: 33.333333333333333333333333%;
  }
}

.home .front-page-3 .listing-item .image {
  min-width: 100%;
  margin: 0;
  display: block;
  position: relative;
}

.home .front-page-3 .listing-item .image * {
  position: relative;
  z-index: 1;
}

.home .front-page-3 .listing-item .image:after {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.82;
  background: #3d65ff;
  background: -moz-linear-gradient(left, #7A28FF 0%, #00A1FF 100%);
  background: -webkit-linear-gradient(left, #7A28FF 0%, #00A1FF 100%);
  background: linear-gradient(to right, #7A28FF 0%, #00A1FF 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7a28ff', endColorstr='#00a1ff', GradientType=1);
}

.home .front-page-3 .listing-item .image:after {
  content: " ";
  display: block;
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
}

.home .front-page-3 .listing-item .image:hover:after, .home .front-page-3 .listing-item .image:focus:after {
  opacity: .82;
}

.home .front-page-3 .listing-item .image:hover + .title, .home .front-page-3 .listing-item .image:focus + .title {
  opacity: 1;
  margin: 0;
}

.home .front-page-3 .listing-item img {
  display: block;
  min-width: 100%;
}

.home .front-page-3 .listing-item .title {
  position: absolute;
  z-index: 3;
  color: #FFFFFF;
  font-size: 20px;
  margin-top: 100px;
  margin-top: 10rem;
  opacity: 0;
  pointer-events: none;
}

.home .front-page-4 {
  background-color: #2A2B33;
}

.home .front-page-4 .widget {
  width: 100%;
}

.home .front-page-4 .textwidget {
