.woocommerce th {
  border: none;
}

.woocommerce span.onsale {
  position: absolute;
  background-color: #2A2B33;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
  font-size: 1.2rem;
  letter-spacing: 0.05em;
  height: 2em;
  width: 4em;
  min-height: 2em;
  min-width: 4em;
  line-height: 1.7;
  left: -0.382em;
  right: auto;
  top: 2em;
}

.woocommerce span.onsale:after {
  content: " ";
  display: block;
  position: absolute;
  border: 0.382em solid transparent;
  border-left-color: black(0.146);
  transform: rotate(-45deg);
  left: 0.1em;
  top: calc(2em - 0.38em);
  border-radius: 5px;
}

.woocommerce .woocommerce-LoopProduct-link {
  display: block;
}

.site-header .fa-shopping-cart {
  position: relative;
  display: block;
